const authCodes = {
  'auth/empty-data': '  ',
  'auth/invalid-email': '  . ',
  'auth/wrong-password': ' ',
  'auth/user-not-found': '  ',
  'auth/too-many-requests': '    ',
  'auth/weak-password': '     6 ',
  'auth/email-already-in-use': '  .   ',
  'auth/alias-already-in-use': '   ',
  'auth/invalid-alias': ' ',
  'auth/alias-format': '      ',
  'auth/user-disabled': ' '
}

export default function convertAuthCode(code) {
  return authCodes[code] || code
}
